.formLabel {
  margin-bottom: 4px;
}

.helperText {
  margin-top: 0;
  margin-bottom: 4px;
}

.group {
  width: fit-content;

  &.row {
    column-gap: 24px;
  }

  &:not(.row) {
    row-gap: 1px;
  }
}

.fullWidth {
  &:global(.MuiFormControl-root),
  :global(.MuiFormGroup-root) {
    width: 100%;
  }
  :global(.MuiFormControlLabel-root) {
    flex-grow: 1;
  }
}
