@import "utils/scss/square-size";

.root {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  padding: 20px;
  border-radius: 4px;
}

.hideCrossIcon {
  display: none;
}
.actionFilled {
  color: var(--primary-ui-color);
}

.actionOutlined {
  color: var(--primary-text-color);
}

/* prettier-ignore */
.blockedIcon {
  fill: var(--error-stroke-color) !important; /* stylelint-disable-line declaration-no-important */
}

.dark {
  background-color: var(--secondary-background-color);
}

.default {
  background-color: var(--white-ui-color);
}

/* prettier-ignore */
.errorIcon {
  fill: var(--error-stroke-color) !important; /* stylelint-disable-line declaration-no-important */
}

.filledSuccess {
  background-color: var(--success-stroke-color);
}

.filledError {
  background-color: var(--error-stroke-color);
}

.filledInfo {
  background-color: var(--info-stroke-color);
}

.filledWarning {
  background-color: var(--warning-stroke-color);

  :global(.MuiAlert-message) {
    color: var(--black-ui-color);
    fill: var(--black-ui-color);
  }

  :global(.button-not-filled) {
    &:hover {
      background-color: rgba(0, 0, 0, 0.1);
    }

    span {
      color: var(--black-ui-color);
    }
  }

  .actionOutlined {
    button {
      color: var(--black-ui-color);
    }
  }

  :global(.MuiAlert-icon) {
    svg {
      fill: var(--black-ui-color);
    }
  }
}

.filledError,
.filledInfo,
.filledSuccess {
  :global(.MuiAlert-icon) {
    svg {
      fill: var(--white-ui-color);
    }
  }
}

/* prettier-ignore */
.helpIcon {
  fill: var(--info-stroke-color) !important; /* stylelint-disable-line declaration-no-important */
}

/* prettier-ignore */
.infoIcon {
  fill: var(--info-stroke-color) !important; /* stylelint-disable-line declaration-no-important */
}

.messageFilled {
  color: var(--white-ui-color);
}

.messageOutlined {
  color: var(--primary-text-color);
}

.outlinedInfo {
  border-color: var(--info-stroke-color);
}

.outlinedSuccess {
  border-color: var(--success-stroke-color);
}

.outlinedError {
  border-color: var(--error-stroke-color);
}

.outlinedWarning {
  border-color: var(--warning-stroke-color);
}

/* prettier-ignore */
.successIcon {
  fill: var(--success-stroke-color) !important; /* stylelint-disable-line declaration-no-important */
}

/* prettier-ignore */
.warningIcon {
  fill: var(--warning-stroke-color) !important; /* stylelint-disable-line declaration-no-important */
}

.alert {
  :global(.MuiAlert-message) {
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding-top: 0;
    padding-bottom: 0;
    font-size: 14px;
    font-weight: 400;
    line-height: 16px;
    word-break: break-word;

    div:first-child {
      margin-right: 10px;
    }
  }

  :global(.button-filled) {
    &:hover {
      background-color: rgba(0, 0, 0, 0.1);
    }

    &:active {
      background-color: rgba(0, 0, 0, 0.2);
    }

    span {
      color: var(--white-text-color);
    }
  }

  :global(.MuiAlert-icon) {
    align-items: center;
    padding-top: 0;
    padding-right: 10px;
    padding-bottom: 0;

    svg:first-child {
      @include square-size(22px);
    }
  }

  :global(.MuiAlert-action) {
    padding-left: 10px;
    margin-left: 0;
  }
}
