@import "utils/scss/square-size";

.toggleButton {
  z-index: auto;
  padding: 7px 24px;
  font-size: 12px;
  font-weight: 600;
  line-height: 16px;
  color: var(--primary-text-color);
  background-color: transparent;
  border-color: var(--primary-stroke-color);
  fill: var(--primary-text-color); /* for svg icons */

  svg {
    @include square-size(16px);
  }

  &:not(:first-child) {
    border-color: var(--primary-stroke-color);
  }

  &.iconButton {
    padding-right: 11px;
    padding-left: 11px;
  }

  &.sizeSmall {
    padding: 3px 24px;
    svg {
      @include square-size(10px);
    }
    &.iconButton {
      padding: 6px 8px;
    }
  }

  &.sizeLarge {
    padding: 11px 24px;
    font-size: 14px;
    &.iconButton {
      padding-right: 13px;
      padding-left: 13px;
    }
  }

  &:focus-visible {
    z-index: 1; /* prevent outline to be hidden by the next button */
    outline-width: 2px;
    outline-style: solid;
    outline-color: var(--primary-stroke-color);
    outline-offset: 1px;
  }

  &:hover {
    background-color: var(--hover-state-color);
  }

  &:active {
    background-color: var(--onclick-state-color);
  }

  &.disabled {
    color: var(--disabled-text-color);
    border-color: var(--disabled-border-color);
    fill: var(--disabled-text-color); /* for svg icons */
  }

  &.selected {
    color: var(--white-text-color);
    background-color: var(--primary-accent-color);
    border-color: var(--primary-stroke-color);
    fill: var(--white-text-color); /* for svg icons */

    &:focus-visible {
      outline-color: var(--primary-accent-color);
    }

    &:hover {
      background-color: var(--primary-hover-color);
    }

    &:active {
      background-color: var(--primary-onclick-color);
    }

    &.disabled {
      color: var(--disabled-text-color);
      background-color: var(--disabled-state-color);
      border-color: var(--disabled-border-color);
      fill: var(--disabled-text-color); /* for svg icons */
    }
  }
}
