.logoButton > .background {
  border-radius: 4px;
  // transition copied from mui Button
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

  &.small {
    padding: 8px;
  }

  &.medium {
    padding: 8px;
  }

  &.large {
    padding: 8px;
  }

  &.extraLarge {
    padding: 16px;
    border-radius: 6px;
  }

  &.slim {
    padding: 16px;
  }
}

.logoButton:focus-visible > .background {
  outline: 2px solid var(--primary-stroke-color);
}

.logoButton:hover > .background {
  background-color: var(--hover-state-color);
}

.logoButton:active > .background {
  background-color: var(--onclick-state-color);
}

.logoButton.disabled > .background {
  background-color: var(--disabled-state-color);
  opacity: 0.32;
}
