.formLabel {
  display: flex;
  font-size: 14px;
  font-weight: 600;
  line-height: 16px;
  color: var(--primary-text-color);
  &.formLabelFocused {
    color: var(--primary-text-color);
  }

  &.disabled {
    color: var(--disabled-text-color);
  }

  &.formLabelError {
    color: var(--error-text-color);
  }
}
