@import "utils/scss/square-size";

@mixin max-square-size($size) {
  max-width: $size;
  max-height: $size;
}

.thumbnail {
  box-sizing: content-box;
  display: flex;
  align-items: center;
  justify-content: center;
}

.small {
  @include square-size(64px);
  .image {
    @include max-square-size(64px);
  }
}

.medium {
  @include square-size(96px);
  .image {
    @include max-square-size(96px);
  }
}

.large {
  @include square-size(124px);
  .image {
    @include max-square-size(124px);
  }
}

.extraLarge {
  @include square-size(148px);
  .image {
    @include max-square-size(148px);
  }
}

.slim {
  width: 100px;
  height: 32px;
  .image {
    max-width: 100px;
    max-height: 32px;
  }
}
