@keyframes bar-color-light {
  0% {
    color: var(--shade-blue-gray-40);
  }
  100% {
    color: var(--white-ui-color);
  }
}

@keyframes bar-color-dark {
  0% {
    color: var(--shade-blue-gray-60);
  }
  100% {
    color: var(--black-ui-color);
  }
}

.backdrop {
  z-index: 99999;
  background: rgba(0, 0, 0, 0.7);
}

.container {
  width: 260px;
}

.percentageText {
  margin-bottom: 20px;
  font-size: 12px;
}

.logo {
  margin-bottom: 16px;
  background-color: transparent;
}

.progressBar {
  height: 14px;
  border: 1px solid var(--tertiary-stroke-color);
  border-radius: 4px;
}

.colorPrimary {
  background-color: var(--shade-blue-gray-80);
}

.bar1Indeterminate,
.bar2Indeterminate {
  background-color: var(--shade-blue-gray-40);
}

.bar1Determinate {
  background: currentColor;
  animation: bar-color-light 100s linear forwards paused;
}

.loadingText {
  margin-top: 16px;
  text-align: center;
}

// design chose combinations for light and dark mode that don't have semantic colors associated
.dark {
  &.backdrop {
    background: rgba(255, 255, 255, 0.7);
  }

  .colorPrimary {
    background-color: var(--shade-blue-gray-20);
  }

  .bar1Indeterminate,
  .bar2Indeterminate {
    background-color: var(--shade-blue-gray-60);
  }

  .bar1Determinate {
    animation-name: bar-color-dark;
  }
}
