@import "utils/scss/square-size";
@import "utils/scss/themed-property";
@import "../constants";

.root {
  box-sizing: content-box;
  width: $switch-root-width;
  height: $switch-root-height;

  // needed for showing the outline (and it also extends the target size)
  padding: calc(#{$switch-outline-width} + #{$switch-outline-offset});

  &.forceRightAlignment {
    // needed to compensate the extra padding (but will overlap with any element on the right)
    margin-right: calc(
      -1 * (#{$switch-outline-width} + #{$switch-outline-offset})
    );
  }
}

.switchBase {
  padding-top: calc(2px + #{$switch-outline-width} + #{$switch-outline-offset});
  padding-left: calc(
    2px + #{$switch-outline-width} + #{$switch-outline-offset}
  );

  // DEFAULT STATE (NOT CHECKED)
  .customThumb {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;

    @include themed-property(
      background-color,
      var(--shade-blue-gray-50),
      var(--shade-blue-gray-30)
    );
    border: 1px solid var(--primary-stroke-color);
    border-radius: 50%;

    @include square-size(16px);
  }

  + .track {
    box-sizing: border-box; // needed to avoid cutting the bottom border
    background: var(--primary-background-color);
    border: 1px solid var(--primary-stroke-color);
    border-radius: 22px;
    opacity: 1;
    transition: none;
  }

  // DEFAULT STATE (NOT CHECKED) - hover
  &:hover {
    background-color: unset; // disable the hover effect near the thumb

    + .track {
      background-color: var(--hover-01-state-color);
    }
  }

  // DEFAULT STATE (NOT CHECKED) - active
  &:active {
    + .track {
      background-color: var(--onclick-01-state-color);
    }
  }

  // DEFAULT STATE (NOT CHECKED) - focus
  &.focusVisible {
    + .track {
      outline: 2px solid var(--primary-stroke-color);
      outline-offset: 1px;
    }
  }

  // // DEFAULT STATE (NOT CHECKED) - disabled
  &.disabled {
    .customThumb {
      @include themed-property(
        background-color,
        var(--disabled-01-state-color),
        var(--disabled-02-state-color)
      );
      border-color: var(--disabled-02-state-color);
    }

    + .track {
      background: var(--primary-background-color);
      border-color: var(--disabled-02-state-color);
      opacity: 1;
    }
  }

  // CHECKED STATE
  &.checked {
    transform: translateX(14px);

    .customThumb {
      background-color: var(--white-ui-color);
      border: none;

      svg {
        @include square-size(12px);
        fill: var(--success-color);
      }
    }

    + .track {
      background-color: var(--success-color);
      border: none;
      opacity: 1;
    }

    // CHECKED STATE - hover
    &:hover {
      background-color: unset; // disable the hover effect near the thumb

      .customThumb {
        svg {
          fill: var(--success-hover-color);
        }
      }

      + .track {
        background-color: var(--success-hover-color);
      }
    }

    // CHECKED STATE - active
    &:active {
      .customThumb {
        svg {
          fill: var(--success-onclick-color);
        }
      }

      + .track {
        background-color: var(--success-onclick-color);
      }
    }

    // CHECKED STATE - focus
    &.focusVisible {
      + .track {
        outline-color: var(--success-color);
      }
    }

    // CHECKED STATE - disabled
    &.disabled {
      .customThumb {
        background-color: var(--white-ui-color);

        svg {
          fill: var(--disabled-01-text-color);
        }
      }

      + .track {
        background: var(--disabled-01-state-color);
        opacity: 1;
      }
    }
  }
}
