@import "utils/scss/square-size";

.tag {
  padding: 4px 0;
  font-size: 18px;
  line-height: 24px;

  &.disabled {
    background-color: var(--quaternary-background-color);
    opacity: 0.8;

    svg {
      fill: var(--shade-blue-gray-40);
    }

    span {
      color: var(--shade-blue-gray-40);
    }
  }
}

.activity {
  background-color: var(--steel-blue-ui-color);
}

.defaultLabel {
  font-weight: 600;
  color: var(--white-text-color);
}

.failure {
  background-color: var(--shade-red-80);
}

.icon {
  @include square-size(15px);
  margin-left: 12px;
  fill: var(--white-text-color);
}

.iconSmall {
  @include square-size(8px);
  margin-left: 8px;
}

.loading {
  background-color: var(--shade-blue-80);
}

.monitorUp {
  background-color: var(--shade-green-80);
}

.monitorDown {
  background-color: var(--shade-red-80);
}

.outlined {
  background-color: var(--tertiary-background-color);
  border: 1px solid var(--quaternary-background-color);

  .icon {
    margin-left: 8px;
    fill: var(--primary-text-color);
  }
}

.outlinedLabel {
  font-weight: 600;
  color: var(--primary-text-color);

  :not(strong) {
    font-weight: 400;
  }
}

.sizeSmall {
  font-size: 12px;
  line-height: 16px;
}

.statusProcessing {
  background-color: var(--shade-yellow-70);

  span {
    color: var(--black-text-color);
  }
}

.statusUploading {
  background-color: var(--shade-blue-gray-70);
}

.success {
  background-color: var(--shade-green-80);
}
