.banner {
  &:global(.MuiAlert-root) {
    position: sticky;
    top: 0;
    z-index: 99998;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    width: 100%;
    padding-right: 8%;
    padding-left: 8%;
    border-radius: 0;
  }

  :global(.MuiAlert-message) {
    font-weight: 600 !important; // stylelint-disable-line declaration-no-important
    word-break: break-word;
  }
}
