.labelRoot {
  width: fit-content;
  margin-right: 0; // to avoid the default margin-right (then the parent component will manage the layout)
  margin-left: 0; // depending on the positioning, the blank left padding should be accounted
}

.label {
  margin-left: 4px;
  color: var(--primary-text-color);

  &.disabled {
    color: var(--disabled-text-color);
  }
}
