@import "utils/scss/square-size";

.root {
  font-weight: 600;
  color: var(--accent-primary-text-color);
  border-radius: 2px;

  svg {
    position: relative;
    top: 2px;
    @include square-size(14px);
    fill: var(--accent-primary-text-color);
  }

  &:hover {
    color: var(--accent-primary-hover-color);
    text-decoration-line: underline;
  }

  &:active {
    color: var(--accent-primary-text-color);
    background-color: var(--onclick-state-color);
    text-decoration-line: underline;
  }

  &:focus-visible {
    outline: 2px solid var(--primary-accent-color);
  }

  &[disabled] {
    color: var(--disabled-text-color);
    pointer-events: none;
  }
}

.colorTextPrimary {
  &,
  &:hover,
  &:active {
    color: var(--primary-text-color);
  }

  svg {
    fill: var(--primary-text-color);
  }

  &:focus-visible {
    outline-color: var(--primary-stroke-color);
  }
}
