@import "../constants";

.labelRoot {
  width: fit-content;
  margin-right: 0; // to avoid the material-ui's default margin-right (then the parent component will manage the layout)
  margin-left: 0; // to avoid the material-ui's default negative margin-left

  &.isInGroup {
    justify-content: space-between; // to keep the label in the left and the switch in the right
    width: 100%;
  }
}

.label {
  // to keep a 10px margin between the visible parts
  margin-right: calc(
    10px - #{$switch-outline-width} - #{$switch-outline-offset}
  );
  font-weight: 600;
  color: var(--secondary-text-color);

  &.disabled {
    color: var(--secondary-text-color);
    opacity: 0.3;
  }
}
