@import "utils/scss/square-size";

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 27px;
  height: 20px;
  padding: 5px;
  background-color: var(--shade-blue-gray-20);
  border: 1px solid var(--shade-blue-gray-50);
  border-radius: 17px;

  &.disabled {
    background-color: var(--white-text-color);
    opacity: 0.3;

    span {
      color: var(--primary-text-color);
      background-color: var(--shade-blue-gray-30);
    }
  }
}

.badge {
  @include square-size(20px);
  font-size: 12px;
  line-height: 14px;
  background-color: var(--shade-blue-gray-90);
}

.icon {
  @include square-size(15px);
  fill: var(--primary-text-color);
}

.dark {
  background-color: var(--shade-blue-gray-70);

  .badge {
    color: var(--shade-blue-gray-100);
    background-color: var(--shade-blue-gray-20);
  }

  &.disabled {
    background-color: var(--shade-blue-gray-100);

    span {
      background-color: var(--shade-blue-gray-80);
    }
  }
}
