@import "utils/scss/themed-property";

.tooltip {
  max-width: 280px;
  padding: 12px;
  font-size: 12px;
  line-height: 16px;
  color: var(--white-text-color);
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
  @include themed-property(
    background-color,
    var(--shade-blue-gray-90),
    var(--shade-blue-gray-80)
  );

  &.fullWidth {
    max-width: unset;
  }
}

.arrow {
  @include themed-property(
    color,
    var(--shade-blue-gray-90),
    var(--shade-blue-gray-80)
  );
}

.anchor {
  display: inline-block;
}
