@import "utils/scss/outline-padding";
@import "utils/scss/square-size";

$button-padding-extrasmall: 7px;
$button-padding-small: 8px;
$button-padding-medium: 12px;
$button-padding-large: 20px;

.iconButton {
  min-width: 0;
  border-radius: 4px;
  svg {
    @include square-size(16px);
  }
}

.extraSmall {
  svg {
    @include square-size(10px);
  }
  padding: $button-padding-extrasmall;
  &:global(.MuiButton-outlined):not(.noBorder) {
    padding: outline-padding($button-padding-extrasmall);
  }
}

.small {
  padding: $button-padding-small;
  &:global(.MuiButton-outlined):not(.noBorder) {
    padding: outline-padding($button-padding-small);
  }
}

.medium {
  padding: $button-padding-medium;
  &:global(.MuiButton-outlined):not(.noBorder) {
    padding: outline-padding($button-padding-medium);
  }
}

.large {
  padding: $button-padding-large;
  &:global(.MuiButton-outlined):not(.noBorder) {
    padding: outline-padding($button-padding-large);
  }
}

.round {
  &.extraSmall {
    border-radius: 14px;
  }

  &.small {
    border-radius: 16px;
  }

  &.medium {
    border-radius: 20px;
  }

  &.large {
    border-radius: 28px;
  }
}

.noBorder {
  border: unset !important; // stylelint-disable-line declaration-no-important
}
