$checkbox-padding: 9px;

// empty left space due to the checkbox padding + the icon blank space
$checkbox-gutter: calc(#{$checkbox-padding} + 2px);

.radio {
  padding: $checkbox-padding;
  color: var(--primary-stroke-color);

  &:hover {
    background-color: var(--hover-state-color);
  }

  &:active {
    background-color: var(--onclick-state-color);
  }

  &:global(.Mui-focusVisible) {
    outline: 2px solid var(--primary-stroke-color);
    outline-offset: -2px;
  }

  &.disabled {
    color: var(--disabled-state-color);
  }

  &.forceLeftAlignment {
    // to compensate the clickable (transparent) left padding
    margin-left: calc(-1 * #{$checkbox-gutter});
  }
}

.checked {
  color: var(--primary-accent-color);
  &:global(.Mui-focusVisible) {
    outline-color: var(--primary-accent-color);
  }
}
